      i {
        cursor: pointer;
        margin-left: 5px;
        &.el-icon-sort-up {
           margin-left: -8px;
        }
        &.el-icon-delete,&.active {
          color: #61a9f8;
        }
      }
    }
    ul {
      &.active {
        margin-top: 5px;
        padding: 0 5px;
        border: 1px dashed #907f7f;
        background: #000;
      }
      height: calc(100% - 16px);
      overflow: auto;
      list-style: none;
      margin: 0;
      padding: 0;
      .row-li {
        display: flex;
        align-items: center;
        margin: 10px 0;
        &.no {
          > .el-input {
            /deep/ .el-input__inner {
              border: 1px dashed #5a5757;
