              text-align: center;
            }
          }
        }
        &:not(.no):hover {
            > .el-input {
              /deep/  .el-input__inner {
                padding-right: 34px;
              }
              /deep/  .el-input__suffix {
                .el-input__suffix-inner i {
                  display: inline-block;
                }
              }
            }
        }
        .el-input {
          /deep/  .el-input__inner {
            padding: 0 0 0 25px;
            cursor: move;
          }
          /deep/  .el-input__prefix {
            color: #61a9f8;
